import gql from 'graphql-tag'

export const FIND_PROJECTS = gql`
    query FindProjectsCustomResolver($filtersInput: FiltersInput!) {
        psprojectsData(input: $filtersInput) {
            _id
            account
            active
            details {
                pm_stage
                pm_project_status
                product_end_date
            }
            opportunity {
                name
                owner
                engagement_manager
            }
            milestones {
                _id
                country
                currency
                name
                summary {
                    delivered_qtd
                    expired_qtd
                    from_training
                    undelivered_hours
                    undelivered_budget
                    unscheduled_budget
                    unscheduled_hours
                }
                base {
                    first_scheduled_date
                    last_scheduled_date
                    hours_for_ns
                    sold_hours
                    planned_hours
                    gap_hours
                    scheduled_hours
                    milestone_amount
                    bill_rate
                }
                schedule {
                    week
                    revenue
                    hours
                }
            }
            name
            owner
            project_manager
            region
            stage
        }
    }
`;